import React from 'react'
import { Button, Form, Input, } from 'antd'
import "./style.less"
import * as TestBtn from "../../img/Snipaste_2023-03-04_20-52-29.png"
import MyNpg from "../../img/Snipaste_2023-03-04_20-52-29.png"

import Icon from "@ant-design/icons"
function MyForm() {
  const [formRef] = Form.useForm()

  const submitEvt = (values) => {
    console.log(values, 'values');
  }

  return (
    <div className='form-con'>
      <Button>
        <Icon
        // component={TestBtn} 

        />
        {/* <img src={MyNpg} alt="" /> */}
      </Button>

      <Form form={formRef} onFinish={submitEvt}
        labelCol={{ span: 6 }}
      >
        <Form.Item label='姓名' name='name'
          rules={[{ required: true, message: 'Please input your name!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label='年龄' name={['info', 0, 'age']}
          rules={[{ required: true, message: 'Please input your age!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label='工龄' name={['info', 0, 'custromers']}
          rules={[{ required: true, message: 'Please input your custromers!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item style={{ textAlign: 'center' }}>
          <Button htmlType='submit' type='primary'>提交</Button>
        </Form.Item>
      </Form>
      <Button
        onClick={() => {
          formRef.setFieldValue({
            info: [{
              age: 2,

              // custromers: 200 
            }
            ]
          })
        }}
      >+++</Button>
    </div>
  )
}

export default MyForm   
